
.Links {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    margin-top: 3.6vh;
    z-index: 100;
}

.Link {
    display: flex;
    margin-bottom: 1.6vh;
    min-width: 30vh;
}

.Link-Image-Content {
    width: 5.6vh;
    height: 5.6vh;
    margin-right: 1.2vh;
}

.Link-Content {
    display: block;
    padding: 0;
    margin: 0;
}

.Link-Content-Caption {
    color: #000000;
    font-family: 'HelveticaNeue', 'Open Sans', Helvetica;
    font-size: 2.4vh;
    line-height: 3.2vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    text-decoration: none;
}

.Link-Content-Description {
    color: #000000;
    font-family: 'HelveticaNeue-Thin', 'Open Sans', Helvetica;
    font-size: 2.0vh;
    line-height: 2.4vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    text-decoration: none;
}
