
.Front {
    padding-top: 4.8vh;
    padding-right: 4.8vh;
    padding-left: 4.8vh;
}

.Header-Caption {
    font-family: 'HelveticaNeue-CondensedBlack', 'Open Sans Condensed', Helvetica;
    font-size: 4.8vh;
    line-height: 4.8vh;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
}

.Header-Description {
    font-family: 'HelveticaNeue', 'Open Sans', Helvetica;
    font-size: 2.4vh;
    line-height: 3.2vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    margin-top: 1.2vh;
}

.Persona {
    position: absolute;
    bottom: 0;
    right: 3.2vh;
    max-width: 48vw;
    max-height: 48vh;
    margin-bottom: -1vh;
    mix-blend-mode: luminosity;
    z-index: -100;
}
