@font-face {
  font-family: 'HelveticaNeue-CondensedBlack';
  src: url(./fonts/HelveticaNeue-CondensedBlack.ttf) format('ttf');
}

@font-face {
  font-family: 'HelveticaNeue';
  src: url(./fonts/HelveticaNeue.ttf) format('ttf');
}

@font-face {
  font-family: 'HelveticaNeue-Thin';
  src: url(./fonts/HelveticaNeue-Thin.ttf) format('ttf');
}

@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&family=Open+Sans:wght@300;400&display=swap');

body {
    color: #000000;
    background: #808080;
    height: 100%;
    padding: 0;
    margin: 0;
    animation: colorchange 32s infinite;
    -webkit-animation: colorchange 32s infinite;
}

@-webkit-keyframes colorchange {
    0%   { background: #F72B37; }
    25%  { background: #4027FF; }
    50%  { background: #FFA900; }
    75%  { background: #4027FF; }
    100% { background: #F72B37; }
}

@keyframes colorchange {
  0%   { background: #F72B37; }
  25%  { background: #4027FF; }
  50%  { background: #FFA900; }
  75%  { background: #4027FF; }
  100% { background: #F72B37; }
}

a {
    text-decoration: none;
}
