@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&family=Open+Sans:wght@300;400&display=swap);
@font-face {
  font-family: 'HelveticaNeue-CondensedBlack';
  src: url(/static/media/HelveticaNeue-CondensedBlack.ca891c1d.ttf) format('ttf');
}

@font-face {
  font-family: 'HelveticaNeue';
  src: url(/static/media/HelveticaNeue.429952c9.ttf) format('ttf');
}

@font-face {
  font-family: 'HelveticaNeue-Thin';
  src: url(/static/media/HelveticaNeue-Thin.87d61be3.ttf) format('ttf');
}

body {
    color: #000000;
    background: #808080;
    height: 100%;
    padding: 0;
    margin: 0;
    animation: colorchange 32s infinite;
    -webkit-animation: colorchange 32s infinite;
}

@-webkit-keyframes colorchange {
    0%   { background: #F72B37; }
    25%  { background: #4027FF; }
    50%  { background: #FFA900; }
    75%  { background: #4027FF; }
    100% { background: #F72B37; }
}

@keyframes colorchange {
  0%   { background: #F72B37; }
  25%  { background: #4027FF; }
  50%  { background: #FFA900; }
  75%  { background: #4027FF; }
  100% { background: #F72B37; }
}

a {
    text-decoration: none;
}


.Front {
    padding-top: 4.8vh;
    padding-right: 4.8vh;
    padding-left: 4.8vh;
}

.Header-Caption {
    font-family: 'HelveticaNeue-CondensedBlack', 'Open Sans Condensed', Helvetica;
    font-size: 4.8vh;
    line-height: 4.8vh;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
}

.Header-Description {
    font-family: 'HelveticaNeue', 'Open Sans', Helvetica;
    font-size: 2.4vh;
    line-height: 3.2vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    margin-top: 1.2vh;
}

.Persona {
    position: absolute;
    bottom: 0;
    right: 3.2vh;
    max-width: 48vw;
    max-height: 48vh;
    margin-bottom: -1vh;
    mix-blend-mode: luminosity;
    z-index: -100;
}


.Links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-top: 3.6vh;
    z-index: 100;
}

.Link {
    display: -webkit-flex;
    display: flex;
    margin-bottom: 1.6vh;
    min-width: 30vh;
}

.Link-Image-Content {
    width: 5.6vh;
    height: 5.6vh;
    margin-right: 1.2vh;
}

.Link-Content {
    display: block;
    padding: 0;
    margin: 0;
}

.Link-Content-Caption {
    color: #000000;
    font-family: 'HelveticaNeue', 'Open Sans', Helvetica;
    font-size: 2.4vh;
    line-height: 3.2vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    text-decoration: none;
}

.Link-Content-Description {
    color: #000000;
    font-family: 'HelveticaNeue-Thin', 'Open Sans', Helvetica;
    font-size: 2.0vh;
    line-height: 2.4vh;
    font-weight: 300;
    padding: 0;
    margin: 0;
    text-decoration: none;
}

